@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Helvetica Narrow", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  background-color: #ffffff;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1460px;
  margin: 0 auto;
}

.show-mobile,
.show-tablet {
  display: none;
}

@media (max-width: 1023px) {
  .show-desktop {
    display: none;
  }

  .show-tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .show-tablet {
    display: none;
  }

  .show-mobile {
    display: block;
  }
}
.header {
  background-color: #1b2d37;
  background-image: url("../../img/header-background-desktop1x.jpg");
  background-size: cover;
  min-height: 700px;
  background-repeat: no-repeat;
  background-position: center;
  color: #ffffff;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  padding-left: 50px;
  padding-right: 50px;
}

.header__topLogo {
  margin-right: auto;
}

.nav {
  display: flex;
  gap: 120px;
  list-style: none;
}

.nav__link {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.promo {
  display: flex;
  padding: 190px 50px;
}

.promo__title {
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 58px;
  color: #ffffff;
}

.info {
  background-color: #35008f;
  min-height: 120px;
  color: white;
}

.info__list {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-align: center;
  padding: 32px 150px 32px;
}

.info__list-item {
  display: flex;
  flex-direction: column;
}

.info__list-number {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 44px;
  letter-spacing: 0.045em;
}

.info__list-text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
}

.table {
  padding: 28px 50px;
}